import chai from 'chai';
import chaiHttp from 'chai-http';
import server from '../server';
import Message from '../models/Message';
chai.use(chaiHttp);
chai.should();

/*
  * Test the /GET route
  */
 describe('/Get All messages', () => {
    it('it should GET all emails/messages', (done) => {
      chai.request(server)
          .get('/api/v1/messages')
          .end((err, res) => {
                res.should.have.status(200);
                res.body.should.be.a('array');
            done();
          });
    });
});

describe('/Get Unread messages', () => {
  it('it should GET all Unread messages', (done) => {
    chai.request(server)
        .get('/api/v1/messages/unread')
        .end((err, res) => {
              res.should.have.status(200);
              res.body.should.be.a('array');
          done();
        });
  });
});

describe('/Get Sent messages', () => {
  it('it should GET all sent messages', (done) => {
    chai.request(server)
        .get('/api/v1/messages/sent')
        .end((err, res) => {
              res.should.have.status(200);
              res.body.should.be.a('array');
          done();
        });
  });
});



describe('/GET/:id book', () => {
  it('it should GET a book by the given id', (done) => {
      let message = new Message({ 
        "id":"65262e7b-0143-486d-b735-630e2fb64396",
        "subject":"Test2",
        "message":"I will came tomorrow",
        "parentMessageId":"556665665",
        "status":"Sent",
        "createdOn":1552243675280
      });
      message.save((err, message) => {
          chai.request(server)
        .get('/api/v1/messages' + message.id)
        .send(message)
        .end((err, res) => {
              res.should.have.status(200);
              res.body.should.be.a('object');
              res.body.should.have.property('subject');
              res.body.should.have.property('message');
              res.body.should.have.property('parentMessageId');
              res.body.should.have.property('status');
              res.body.should.have.property('createdOn');
              res.body.should.have.property('_id').eql(message.id);
          done();
        });
      });

  });
});